.login-container{overflow: hidden;}
.login {
	width: 400px;margin: 120px auto;padding: 35px 35px 15px 35px;
  color: #848a9a;
  text-align: center;
  position: relative;box-sizing: border-box;
  .title{font-size: 26px;font-weight: bold;color: #eee;margin-bottom: 50px;}
  .logo {
    height: 75px;
    width: 75px;
    background-image: url(images/logos/main.svg);
    background-repeat: no-repeat;
    background-size: 39px;
    background-position: center center;
    border-radius: 50%;
    margin: 0 auto;
    background-color: $primary;
    z-index: 5;
  }
  .btn{width: 100%;background-color: #409eff;border-color: #409eff;
  	&:hover, &:focus{background-color: #66b1ff;border-color: #66b1ff;}
  	&:active{background-color: #3a8ee6;border-color: #3a8ee6}
  }
  H1 {
    font-size: 28px;
    color: white;
    word-break: normal;
  }

  SECTION {
  	padding: 0;
    background-color: initial;
    .form-group{
    	input{
    		border: 1px solid hsla(0,0%,100%,.1);
    		background: rgba(0,0,0,.1);
    		color: #eee;
    		border-radius: 4px;
    	}
    	::-webkit-input-placeholder{
	      color: rgba(238,238,238, .5);
	    }
    	label{color: #eee;margin-bottom: 5px;font-size: 12px;}
    }
  }
}
.language-span{position: absolute;right: 0;top: 0;z-index: 1;
  .btn-link{color: #fff}
}
